				"bodyHTML": "<p>I've gotten involved in a number of new projects in the past 6-7 months so thought I'd update y'all in what I'm doing.</p>\n<h3>School:</h3>\n<p>After a lovely summer break with no school and my time spent vacationing and working at a number of jobs, I came back to <a href=\"http://byu.edu\">BYU</a> for my first semester in the <a href=\"http://isys.byu.edu\">Masters of Information Systems program</a>. But, as my classes this past semester were pretty boring, I won't mention school anymore here.</p>\n<h3>E-commerce websites:</h3>\n<p>I've built two e-commerce sites over the past six months using <a href=\"http://ubercart.org\">Ubercart</a> (a fantastic open source shopping cart -- if you're in the market for that sort of thing). Ubercart is a module (cf. plugin/extension) for <a href=\"http://drupal.org\" title=\"drupal\">Drupal</a>, an open source <a href=\"http://en.wikipedia.org/wiki/Content_management_system\">content management system</a>, another technology I've been working with extensively.</p>\n<p>This past June, with two friends, I started a t-shirt company running out of our apartment. As the lone techie, I built our website at <a href=\"http://coolcamisa.com\">coolcamisa.com</a>. By our expectations, the company has been a rousing success (i.e., we didn't lose money)! We paid back our start-up costs by September (2 1/2 months from launch) and have netted some two grand since then. Not bad for our little apartment-based bootstrapped business. I've learned tons about starting a business, legal stuff (we organized as an LLC), pricing, marketing, website development, etc.</p>\n<p>My second e-commerce site I started just recently. A good friend of mine, Nicole Sheahan, released her first CD in December (She's an excellent singer btw, <a href=\"http://nicolesheahan.com/product/invisible-facts\">so go buy her cd</a>). I've been helping her set up a website to sell / market her music. The site is still a work in progress but it's amazing to me how fast it has come together. I've been working with open source software for awhile but I'm still impressed that I can singlehandedly put together a fully functional ecommerce store in under 10 hours for no cost other then my labor. That's the power of a community.</p>\n<h3>Other (Drupal) web sites:</h3>\n<p>With a friend, I've been volunteering to build a website for the Utah Valley Ministerial Association. The <a href=\"http://utahvalleyinterfaith.org\">website</a> will have news and events from churches in Utah County and serve as an online directory of churches in Utah County and surrounding communities. The site is not yet fully operational but it's soon in coming. Drupal's \"<a href=\"http://drupal.org/project/views\">Views</a>\" module was the real workhorse here slicing and dicing the church listings for display in a number of different ways. As you can see on the website, the different \"views\" of the data enable a user to find churches by city, denomination, or language.</p>\n<h4>Family History Site</h4>\n<p>Because of my Mormon heritage, the genealogy of my ancestors is very well done. Most of my ancestoral lines are traced back to the 17-18th centuries. About two years ago, my siblings/parents and I decided that while it's great to have names, dates, and places from our ancestors, what is really interesting is to read the stories and letters they wrote and view pictures of them and where they lived. These stories/letters/pictures/etc. flesh out the skeletal data we see on the normal family history tree.</p>\n<p>But while current genealogical software does very well at organizing, storing, and sharing genealogical data, it does a poor job at handling other types of information. Documents and pictures tend to get lost in attics of aunts/uncles/grandparents. If you don't have them, they're hard to get hold of and you have them, they're hard to share with relatives.</p>\n<p>We decided the best solution to this problem would be to create a website. Our first answer was a <a href=\"http://familyhistory.mathews2000.com/\">wiki</a> <a href=\"http://bunker.mathews2000.com/\">website</a>. The wiki software made it easy for our widely distributed family to work together on gathering information. But after some time we grew dissatisfied with the limitations of our wiki software and decided to migrate our site to one based on Drupal. That migration is still in progress but we're excited as Drupal will give us much more control over how information is stored and displayed (albeit with a great deal more complexity).</p>\n<h3>A New Job</h3>\n<p>In June, I started working as an research assistant at <a href=\"http://byu.edu\">BYU</a> in the <a href=\"http://ebusiness.byu.edu/\">Kevin &#x26; Debra Rollin's Center for eBusiness</a>.</p>\n<p>I'm working with a professor and a pH.D student studying how web2.0 technologies can be used in the classroom to help students become more active learners.</p>\n<p>So far I can say this is the best job I've ever had. What I'm doing and learning just fascinates me.</p>\n<p>Our main research thrust this past semester was creating a community site for a class to use at BYU -- <a href=\"http://isyscore.byu.edu/drupal\">http://isyscore.byu.edu/drupal</a>.</p>\n<p>We have learned a lot from the experience -- and it's given us mountains of data to sift through -- more on that in the future hopefully. Building the site showed me again the power Drupal gives a website developer (even if they really don't know what they're doing) to create sophisticated sites.</p>\n<p>My other big project for my research job is creating a new module for Drupal called <a href=\"http://drupal.org/project/writing_assignment\">writing_assignment</a>. This module will make it easy for teachers to give students writing assignments on a Drupal website. It will also facilitate easy peer-review and peer-grading of those assignments. This is my first Drupal module plus my first extensive project in PHP so I have faced quite the learning curve. You can read more about my module <a href=\"http://groups.drupal.org/node/7401\">here</a>.</p>\n",
